import 'rxjs/add/operator/switchMap';
import { Component, OnInit }        from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location }                 from '@angular/common';

import { Store }        from './store';
import { StoreService } from './store.service';

@Component({
  selector: 'store-detail',
  templateUrl: './store-detail.component.html',
  styleUrls: [ './store-detail.component.css' ]
})
export class StoreDetailComponent implements OnInit {
  store: Store;

  constructor(
    private storeService: StoreService,
    private route: ActivatedRoute,
    private location: Location
  ) {}

  ngOnInit(): void {
    let id = this.route.snapshot.params['id'];
    this.storeService.getStore(this.onSuccess.bind(this), this.onError.bind(this), id);

  }

  onSuccess(model, response): void {
    console.log(response);
    
    this.route.paramMap.subscribe(store => this.store = response);
    
  }

  onError(model, response): void {

  }

  save(): void {
    this.storeService.update(this.store)
      .then(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}



