import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Store } from './store';

declare const Baas: any;

@Injectable()
export class StoreService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private StoresUrl = 'api/stores';  // URL to web api

  constructor(private http: Http) {
      Baas.API_KEY = '6f25e0bc-f527-4bd0-824d-fcad7c4f2e91';
  }

  getStores(onSuccess, onError): void {
    let table = Baas.Table.create('store');

    table.fetch({
      success: onSuccess,
      error: onError
    });
  }

  getStore(onSuccess, onError, objectId: string) {
    let obj = Baas.Object.create('store', {objectId});
    obj.fetch({
      success: onSuccess,
      error: onError
    });
  }


  getStore2(objectId: string): Promise<Store> {
    const url = `${this.StoresUrl}/${objectId}`;
    return this.http.get(url)
      .toPromise()
      .then(response => response.json().data as Store)
      .catch(this.handleError);
  }

  delete(objectId: string): Promise<void> {
    const url = `${this.StoresUrl}/${objectId}`;
    return this.http.delete(url, {headers: this.headers})
      .toPromise()
      .then(() => null)
      .catch(this.handleError);
  }

  create(name: string): Promise<Store> {
    return this.http
      .post(this.StoresUrl, JSON.stringify({name: name}), {headers: this.headers})
      .toPromise()
      .then(res => res.json().data as Store)
      .catch(this.handleError);
  }

  update(store: Store): Promise<Store> {
    const url = `${this.StoresUrl}/${store.objectId}`;
    return this.http
      .put(url, JSON.stringify(store), {headers: this.headers})
      .toPromise()
      .then(() => store)
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}




