import {Component, OnInit} from '@angular/core';

import {Store} from './store';
import {StoreService} from './store.service';

declare const Baas: any;
@Component({
  selector: 'my-store-list',
  templateUrl: './store-list.component.html',
  styleUrls: ['./store-list.component.css']
})
export class StoreListComponent implements OnInit {
  stores: Store[] = [];

  constructor(private storeService: StoreService) {}

  ngOnInit(): void {
    this.getObject();
  }

  getObject(): void {
    this.storeService.getStores(this.onSuccess.bind(this), this.onError.bind(this));
  }

  onSuccess(model, response): void {
    this.stores = response.results;
  }

  onError(model, response): void {

  }
}




